.game-container {
  display: flex;
  flex-direction: row;
  padding: 20px;
  gap: 20px;
  max-width: 100%;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .game-container {
    flex-direction: column-reverse;
    padding: 10px;
    gap: 10px;
  }
}

.chessboard {
  position: relative;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  width: 560px;
  height: 560px;
  border: 4px solid #3a3a3a;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

@media (max-width: 768px) {
  .chessboard {
    width: 100%;
    height: auto;
    aspect-ratio: 1/1; /* Maintain square aspect ratio */
    max-width: 95vmin;
    margin: 0 auto;
  }
}

.captured-pieces {
  width: 250px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

@media (max-width: 768px) {
  .captured-pieces {
    width: 100%;
    max-width: 95vmin;
    margin: 0 auto;
    order: 2; /* Put captured pieces below the board */
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  
  .captured-pieces h3 {
    grid-column: span 2;
  }
  
  .turn-info {
    grid-column: span 2;
  }
  
  .restart-button {
    grid-column: span 2;
  }
}

.square {
  width: 100px;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
}

.square.selected {
  background-color: rgba(255, 206, 84, 0.5) !important;
  box-shadow: inset 0 0 20px rgba(255, 206, 84, 0.3);
}

.square.highlighted {
  position: relative;
}

.square.highlighted::after {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: rgba(105, 171, 111, 0.4);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease;
}

.square:hover {
  cursor: pointer;
}

.square:hover::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.1);
  transition: background-color 0.2s ease;
}

.square.white {
  background-color: #ffffff;
}

.square.black {
  background-color: #808080;
}

.piece {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 5px;
}

.piece-icon {
  width: 70px;
  height: 70px;
  margin-bottom: 0;
  transition: transform 0.2s ease;
}

.piece-icon:hover {
  transform: scale(1.05);
}

.player-image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
  transition: transform 0.2s ease;
  z-index: 10;
}

.player-image:hover {
  transform: scale(1.05);
}

.piece-name {
  font-size: 11px;
  text-align: center;
  width: 90px;
  font-weight: bold;
  margin-top: -10px;
  color: #000000;
  background-color: transparent;
  padding: 2px;
  border-radius: 3px;
  z-index: 3;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.8);
}

.board {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  width: 100%;
  height: 100%;
  border: 2px solid #333;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.square {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s;
}

.square:hover {
  background-color: rgba(255, 255, 0, 0.2);
}

.square.white { 
  background-color: #ffffff;
}

.square.black { 
  background-color: #808080;
}

.selected {
  background-color: #7389cc !important;
}

.valid-move::before {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(106, 190, 47, 0.5);
  border: 3px solid rgba(106, 190, 47, 0.8);
  border-radius: 50%;
  z-index: 1;
  pointer-events: none;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { transform: translate(-50%, -50%) scale(1); opacity: 0.8; }
  50% { transform: translate(-50%, -50%) scale(1.1); opacity: 0.5; }
  100% { transform: translate(-50%, -50%) scale(1); opacity: 0.8; }
}

.piece {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.piece-icon {
  width: 80%;
  height: auto;
  transition: transform 0.2s;
}

.selected .piece-icon {
  transform: scale(1.1);
}

.captured-pieces {
  width: 250px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.captured-pieces h3 {
  margin: 5px 0;
  text-align: center;
}

.captured-pieces h4 {
  margin: 5px 0;
  text-align: center;
}

.captured-red, .captured-blue {
  margin-bottom: 15px;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
}

.captured-piece {
  display: flex;
  align-items: center;
  margin: 5px 0;
  padding: 4px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.7);
  position: relative; /* Required for tooltip positioning */
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.captured-piece:hover {
  background-color: rgba(230, 230, 230, 1);
}

.captured-piece-icon {
  width: 30px;
  height: 30px;
  object-fit: contain;
  margin-right: 8px;
}

.captured-piece-name {
  font-size: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.captured-meme-tooltip {
  position: absolute;
  left: 110%;
  top: 50%;
  transform: translateY(-50%);
  z-index: 100;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  padding: 5px;
  animation: tooltip-fade-in 0.2s ease;
}

@media (max-width: 768px) {
  .captured-meme-tooltip {
    left: auto;
    right: 110%;
    max-width: 150px;
  }
  
  .captured-meme-image {
    max-width: 140px;
    max-height: 140px;
  }
  
  /* Make captured piece rows more touch-friendly */
  .captured-piece {
    padding: 8px 4px;
    margin: 8px 0;
  }
  
  .captured-piece-name {
    font-size: 12px;
  }
}

@keyframes tooltip-fade-in {
  from {
    opacity: 0;
    transform: translateY(-50%) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(-50%) scale(1);
  }
}

.captured-meme-image {
  max-width: 180px;
  max-height: 180px;
  display: block;
  border-radius: 5px;
}

.meme-image {
  object-fit: cover;
  transition: transform 0.3s ease;
}

.meme-image:hover {
  transform: scale(1.2);
}

.turn-info {
  margin: 15px 0;
  text-align: center;
}

.restart-button {
  margin-top: 15px;
  width: 100%;
}

.move-indicator {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 255, 0, 0.3);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0.7;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.1);
    opacity: 0.9;
  }
  100% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0.7;
  }
}

.game-info {
  position: absolute;
  bottom: -80px;
  right: 0;
  width: 800px;
  text-align: center;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.game-info h3 {
  margin: 0;
  color: #333;
}

.game-info p {
  font-weight: bold;
  color: #d32f2f;
  margin: 5px 0;
  font-size: 18px;
}

/* Winner overlay styles */
.winner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.winner-content {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
  animation: winner-animation 0.5s ease-in-out;
}

@keyframes winner-animation {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.winner-content h2 {
  font-size: 36px;
  margin: 0 0 15px 0;
  color: #d32f2f;
}

.winner-content p {
  font-size: 18px;
  margin-bottom: 20px;
}

.restart-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.restart-button:hover {
  background-color: #388e3c;
}

.game-info .restart-button {
  margin-top: 10px;
}

/* Captured Piece Popup Styles */
.captured-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  cursor: pointer;
  touch-action: manipulation; /* Better touch behavior */
}

.captured-popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  max-width: 90%;
  max-height: 90%;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(255, 255, 255, 0.3);
  animation: popup-animation 0.4s ease-in-out;
}

@keyframes popup-animation {
  0% {
    transform: scale(0.7);
    opacity: 0;
  }
  70% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.captured-popup-image {
  max-width: 90%;
  max-height: 60vh;
  object-fit: contain;
  border-radius: 5px;
  margin-bottom: 15px;
}

@media (max-width: 768px) {
  .captured-popup-content {
    padding: 15px;
    max-width: 95%;
  }
  
  .captured-popup-image {
    max-height: 40vh;
  }
  
  .captured-popup-name {
    font-size: 20px;
  }
  
  .captured-popup-instruction {
    font-size: 14px;
  }
}
