.App {
  text-align: center;
  min-height: 100vh;
  background-color: #1e1e1e;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

@media (max-width: 768px) {
  main {
    padding: 5px;
  }
}

/* Add responsive typography */
html {
  font-size: 16px;
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}

/* Ensure all images are responsive */
img {
  max-width: 100%;
  height: auto;
}
